<script>
import Web3 from "web3";
import tokenABI from "@/abi/token.js";
import pool0ABI from "@/abi/pool0.js";
import pool1ABI from "@/abi/pool1.js";
import erc20ABI from "@/abi/erc20.js";
import pairABI from "@/abi/pair.js";

let web3;
let pandaContract;
let pool0Contract;
let pool1Contract;
let yUSDContract;
let pairContract;

let pool0Timer;
let pool1Timer;

export default {
  data() {
    return {
      pandaBalance: "-",
      contractAddress: {
        panda: "0x0Efe4BDBE1Ff36C0C2F14Be62A340c0EeB5ba0C7",
        pool0: "0x2424C4aBf7755C18c3F46058f864233F5213b796",
        pool1: "0xdaA6730BDcB34B567e241a486CA47eD36F56266B",
        yUSD: "0x35948e08e2F65531C608A9d58a225B582b80824b",
        pair: "0x5E30DFFbD244E4C6d5e83F1c69Bd9625Cf74A8c9",
      },
      defaultAccount: "",
      selectPanel: "",
      selectModal: "",
      pool0: {
        balance: "-",
        reward: "-",
        staked: "-",
        stakeInput: "",
        unstakeInput: "",
      },
      pool1: {
        balance: "-",
        reward: "-",
        staked: "-",
        stakeInput: "",
        unstakeInput: "",
      },
    };
  },
  mounted() {
    this.init();
  },
  methods: {
    async init() {
      await this.checkWallet();
      this.initContract();
    },
    initContract() {
      pandaContract = new web3.eth.Contract(
        tokenABI,
        this.contractAddress.panda
      );

      pool0Contract = new web3.eth.Contract(
        pool0ABI,
        this.contractAddress.pool0
      );
      pool1Contract = new web3.eth.Contract(
        pool1ABI,
        this.contractAddress.pool1
      );
      yUSDContract = new web3.eth.Contract(erc20ABI, this.contractAddress.yUSD);
      if (this.contractAddress.pair) {
        pairContract = new web3.eth.Contract(
          pairABI,
          this.contractAddress.pair
        );
      }
    },
    async showPool0() {
      let flag = await this.checkWallet();
      if (!flag) {
        return;
      }
      this.selectPanel = "pool0";
      this.getPool0Data();
    },
    async showPool1() {
      let flag = await this.checkWallet();
      if (!flag) {
        return;
      }
      this.selectPanel = "pool1";
      this.getPool1Data();
    },
    async getPool0Data() {
      let balance = await yUSDContract.methods
        .balanceOf(this.defaultAccount)
        .call({ from: this.defaultAccount });
      let reward = await pool0Contract.methods
        .earned(this.defaultAccount)
        .call({ from: this.defaultAccount });
      let staked = await pool0Contract.methods
        .balanceOf(this.defaultAccount)
        .call({ from: this.defaultAccount });
      this.pool0.balance = this.ellipsisUnit(balance);
      this.pool0.reward = this.ellipsisUnit(reward);
      this.pool0.staked = this.ellipsisUnit(staked);
      this.loopPool0Reward();
    },
    async loopPool0Reward() {
      if (pool0Timer) {
        return;
      }
      pool0Timer = setInterval(async () => {
        let reward = await pool0Contract.methods
          .earned(this.defaultAccount)
          .call({ from: this.defaultAccount });
        this.pool0.reward = this.ellipsisUnit(reward);
      }, 3000);
    },
    async getPool0Reward() {
      await pool0Contract.methods.getReward().send({
        from: this.defaultAccount,
        gasLimit: "250000",
      });
    },
    async pool0stake() {
      let stakeNum = this.toWei(this.pool0.stakeInput);
      let balance = await yUSDContract.methods
        .balanceOf(this.defaultAccount)
        .call({ from: this.defaultAccount });
      if (Number(stakeNum) > Number(balance)) {
        stakeNum = balance;
      }
      let approveNum = await yUSDContract.methods
        .allowance(this.defaultAccount, this.contractAddress.pool0)
        .call({
          from: this.defaultAccount,
        });
      if (Number(approveNum) < Number(stakeNum)) {
        await yUSDContract.methods
          .approve(this.contractAddress.pool0, "10000000000000000000000000000")
          .send({
            from: this.defaultAccount,
            gasLimit: "70000",
          });
      }
      await pool0Contract.methods.stake(stakeNum).send({
        from: this.defaultAccount,
        gasLimit: "300000",
      });
      this.closeModal();
      this.getPool0Data();
    },
    async pool0unstake() {
      let amount = this.toWei(this.pool0.unstakeInput);
      let staked = await pool0Contract.methods
        .balanceOf(this.defaultAccount)
        .call({ from: this.defaultAccount });
      if (Number(amount) > Number(staked)) {
        amount = staked;
      }
      await pool0Contract.methods.withdraw(amount).send({
        from: this.defaultAccount,
        gasLimit: "200000",
      });
      this.closeModal();
      this.getPool0Data();
    },
    async existPool0() {
      await pool0Contract.methods.exit().send({
        from: this.defaultAccount,
        gasLimit: "200000",
      });
      this.getPool0Data();
    },
    async getPool1Data() {
      let balance = await pairContract.methods
        .balanceOf(this.defaultAccount)
        .call({ from: this.defaultAccount });
      let reward = await pool1Contract.methods
        .earned(this.defaultAccount)
        .call({ from: this.defaultAccount });
      let staked = await pool1Contract.methods
        .balanceOf(this.defaultAccount)
        .call({ from: this.defaultAccount });
      this.pool1.balance = this.ellipsisUnit(balance);
      this.pool1.reward = this.ellipsisUnit(reward);
      this.pool1.staked = this.ellipsisUnit(staked);
      this.loopPool1Reward();
    },
    async loopPool1Reward() {
      if (pool1Timer) {
        return;
      }
      pool1Timer = setInterval(async () => {
        let reward = await pool1Contract.methods
          .earned(this.defaultAccount)
          .call({ from: this.defaultAccount });
        this.pool1.reward = this.ellipsisUnit(reward);
      }, 3000);
    },
    async getPool1Reward() {
      await pool1Contract.methods.getReward().send({
        from: this.defaultAccount,
        gasLimit: "250000",
      });
    },
    async pool1stake() {
      let stakeNum = this.toWei(this.pool1.stakeInput);
      let balance = await pairContract.methods
        .balanceOf(this.defaultAccount)
        .call({ from: this.defaultAccount });
      if (Number(stakeNum) > Number(balance)) {
        stakeNum = balance;
      }
      let approveNum = await pairContract.methods
        .allowance(this.defaultAccount, this.contractAddress.pool0)
        .call({
          from: this.defaultAccount,
        });
      if (Number(approveNum) < Number(stakeNum)) {
        await pairContract.methods
          .approve(this.contractAddress.pool1, "10000000000000000000000000000")
          .send({
            from: this.defaultAccount,
            gasLimit: "70000",
          });
      }
      await pool1Contract.methods.stake(stakeNum).send({
        from: this.defaultAccount,
        gasLimit: "300000",
      });
      this.closeModal();
      this.getPool1Data();
    },
    async pool1unstake() {
      let amount = this.toWei(this.pool1.unstakeInput);
      let staked = await pool1Contract.methods
        .balanceOf(this.defaultAccount)
        .call({ from: this.defaultAccount });
      if (Number(amount) > Number(staked)) {
        amount = staked;
      }
      await pool1Contract.methods.withdraw(amount).send({
        from: this.defaultAccount,
        gasLimit: "200000",
      });
      this.closeModal();
      this.getPoo1Data();
    },
    async existPool1() {
      await pool1Contract.methods.exit().send({
        from: this.defaultAccount,
        gasLimit: "200000",
      });
      this.getPool1Data();
    },
    async checkWallet() {
      if (web3 && this.defaultAccount) {
        return true;
      }
      if (window.ethereum) {
        web3 = new Web3(window.ethereum);
        try {
          await window.ethereum.enable();
        } catch (error) {
          throw new Error(error);
          return false;
        }
      } else if (window.web3) {
        web3 = new Web3(window.web3.currentProvider);
      } else {
        throw new Error(
          "Non-Ethereum browser detected. You should consider trying MetaMask!"
        );
        return false;
      }
      const accounts = await web3.eth.getAccounts();
      this.defaultAccount = accounts[0];
      console.log(this.defaultAccount);
      return true;
    },
    async showWallet() {
      this.showModal("wallet");
      let amount = await pandaContract.methods
        .balanceOf(this.defaultAccount)
        .call({ from: this.defaultAccount });
      this.pandaBalance = this.ellipsisUnit(amount);
    },
    showModal(name) {
      this.selectModal = name;
    },
    closeModal() {
      this.selectModal = "";
    },
    toWei(value, unit = "ether") {
      return web3.utils.toWei(value, unit);
    },
    fromWei(value, unit = "ether") {
      return web3.utils.fromWei(value, unit);
    },
    ellipsisUnit(value) {
      if (Number(value) === 0) {
        return 0;
      }
      let amount = this.fromWei(value);
      return Number(amount).toFixed(4);
    },
    hiddenAddress(address) {
      let a = address.substring(0, 6);
      let b = "****";
      let c = address.substring(address.length - 4);
      return a + b + c;
    },
  },
};
</script>

